.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  transition: top 0.3s ease;

  &.hide-navigation {
    .lheader__navigation {
      width: 0;
      padding: 0;
      visibility: hidden;
      opacity: 0;
    }
    .lheader__search {
      width: 100%;
    }
  }

  &.hide-mobile-primary-button {
    .mheader__button {
      width: 0;
      margin: 0;
      background: transparent;
      visibility: hidden;
      opacity: 0;
      transform: translateX(20px);
    }
    .mheader__search {
      width: 100%;
    }
  }
}

.uheader {
  background: #007953;
  height: 36px;

  @media (max-width: 1199.98px) {
    display: none;
  }

  &__inner {
    display: flex;
    justify-content: flex-end;
    padding: 8px 5px;
  }

  &__pickers {
    margin-left: 24px;

    @media (min-width: 1200px) and (max-width: 1450px) {
      .country-picker {
        .country-picker__dropdown {
          left: auto;
          right: -92px;
        }
      }
      .language-picker {
        .language-picker__dropdown {
          left: auto;
          right: -20px;
        }
      }
    }
  }
}

.lheader {
  background: #fff;
  border-bottom: 1px solid #ebf0f5;
  height: 90px;

  @media (max-width: 1199.98px) {
    display: none;
  }

  &__inner {
    display: flex;
    align-items: center;
    height: 90px;
  }

  &__logotype {
    margin: 12px 48px 7px 3px;
  }

  &__navigation {
    margin-right: 49px;
    transition: opacity 0.3s ease;
  }

  &__search {
    margin-left: auto;
    margin-right: 0;
  }

  &__buttons {
    display: flex;
    align-items: center;
    margin-left: 24.5px;
    margin-right: 0;
  }

  &__button {
    .primary-button {
      white-space: nowrap;
      height: 40px;
    }
    .outlined-button {
      white-space: nowrap;
      height: 40px;
    }
  }

  &__button + &__button {
    margin-left: 24px;
  }
}

.mheader {
  position: relative;
  background: #ffffff;
  height: 60px;

  @media (min-width: 1200px) {
    display: none;
  }

  &__top {
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
  }

  &__inner {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px 0;
  }

  &__logo {
    margin-left: 0;
    margin-right: auto;
  }

  &__button {
    margin-left: 16px;
    transition: opacity 0.3s ease;

    .primary-button {
      white-space: nowrap;
    }
  }

  &__search {
    margin-left: 16px;
  }

  &__burger {
    margin-left: 16px;
  }

  &__menu {
    width: 100%;
  }
}

.logotype {
  display: block;
  width: 200px;
  height: 70px;

  img,
  svg {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.logo {
  display: block;
  width: 31px;
  height: 36px;

  img,
  svg {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.banner {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  background: #ebf0f5;
  transition: 0.3s;
  transform: translateY(-60px);

  &.active {
    transform: translateY(0);
  }

  @media (min-width: 768px) {
    display: none;
  }

  &__inner {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 12px 20px 12px 16px;
  }

  &__close {
    width: 12px;
    height: 12px;
    margin-right: 17px;
    flex-shrink: 0;

    img,
    svg {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__image {
    width: 36px;
    height: 36px;
    margin-right: 13px;
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: #556176;
  }

  &__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: #708090;
  }

  &__link {
    margin-left: auto;
    margin-right: 0;
  }
}

.banner + .header {
  top: 0px;

  @media (max-width: 767.98px) {
    top: 60px;
  }
}

.admin-bar {
  .banner {
    top: 32px;

    @media (max-width: 767.98px) {
      top: 46px;
    }
  }

  .header {
    top: 32px;

    @media (max-width: 782.98px) {
      top: 46px;
    }

    @media (max-width: 767.98px) {
      top: 46px;
    }
  }

  .banner + .header {
    @media (max-width: 767.98px) {
      top: 106px;
    }
  }
}

.banner + .header.hide-banner {
  top: 0px;
}

.admin-bar {
  .header.hide-banner {
    top: 32px;

    @media (max-width: 782.98px) {
      top: 46px;
    }

    @media (max-width: 767.98px) {
      top: 46px;
    }
  }
}

#wpadminbar {
  position: fixed;
  left: 0;
  top: 0;
}
