  .counter {
    position: relative;
    background: #ffffff;
    border: 1px solid #a5adb4;
    border-radius: 5px;
    padding: 16px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    min-width: 110px;
    height: 48px;
  }
  .counter__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    color: #748994;
  }
  .counter__button {
    width: 16px;
    height: 16px;
    border: 1px solid #219963;
    border-radius: 50px;
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    line-height: 100%;
    color: #219963;
  }
  .counter__button.disabled {
    border-color: #a5adb4;
    color: #a5adb4;
  }
  .counter__button.disabled::after,
  .counter__button.disabled::before {
    background: #a5adb4 !important;
  }
  .counter__button--minus {
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
  }
  .counter__button--minus::after {
    position: absolute;
    top: 50%;
    left: 50%;
    content: "";
    height: 1px;
    width: 80%;
    border-radius: 10px;
    background: #219963;
    transform: translate(-50%, -50%);
  }
  .counter__button--plus {
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
  }
  .counter__button--plus::before {
    position: absolute;
    top: 50%;
    left: 50%;
    content: "";
    height: 80%;
    width: 1px;
    border-radius: 10px;
    background: #219963;
    transform: translate(-50%, -50%);
  }
  .counter__button--plus::after {
    position: absolute;
    top: 50%;
    left: 50%;
    content: "";
    height: 1px;
    width: 80%;
    border-radius: 10px;
    background: #219963;
    transform: translate(-50%, -50%);
  }

