  .text ul > li::before {
    content: "";
    position: absolute;
    left: 10px;
    top: 9px;
    width: 6px;
    height: 6px;
    background: #748994;
    border-radius: 50%;
  }
  .text ol {
    list-style: none;
    counter-reset: ol;
  }
  .text ol > li {
    position: relative;
    padding-left: 23px;
  }
  .text ol > li::before {
    counter-increment: ol;
    content: counter(ol) ".";
    position: absolute;
    left: 0px;
    top: 0px;
  }
  .text ol ol {
    counter-reset: ol2;
  }
  .text ol ol > li:before {
    counter-increment: ol2;
    content: counter(ol1) "." counter(ol2) ".";
  }
