      padding-top: 12px;
      padding-bottom: 29px;
      border-top: none;
    }
  }
  .footer__text-content {
    font-size: 14px;
    line-height: 150%;
    color: #748994;
  }
  @media (max-width: 575.98px) {
    .footer__text-content {
      font-size: 12px;
    }
  }

  .social-media__inner {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .social-media__item {
    display: inline-flex;
    height: 24px;
    width: 24px;
  }
  @media (max-width: 767.98px) {
    .social-media__item {
      height: 32px;
      width: 32px;
    }
  }

  .social-media__item + .social-media__item {
    margin-left: 16px;
  }

  .copyright {
    display: block;
    font-size: 16px;
    line-height: 150%;
    color: #ffffff;
  }
  @media (max-width: 767.98px) {
    .copyright {
      font-size: 12px;
      line-height: 150%;
    }
  }
